.main-nav__toggler {
  width: 40px;
  height: 45px;
  margin: auto 0;
  position: relative;
  border: none;
  background: none;
  padding: 5px;
  display: block;
  cursor: pointer;
}
@media (min-width: 768px) {
  .main-nav__toggler {
    display: none;
  }
}

@media (max-width: 768px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}

.main-nav--closed .main-nav__toggler::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  width: 24px;
  height: 2px;
  background-color: black;
  box-shadow: 0 8px 0 0 black, 0 16px 0 0 black;
}

.main-nav--opened .main-nav__toggler::before, .main-nav--opened .main-nav__toggler::after {
  content: "";
  position: absolute;
  top: 20px;
  left: 5px;
  width: 36px;
  height: 2px;
  background-color: black;
}
.main-nav--opened .main-nav__toggler::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggler::after {
  transform: rotate(-45deg);
}
.main-nav--opened .main-nav__toggler:active::before, .main-nav--opened .main-nav__toggler:active::after {
  background-color: black;
}

.main-nav--nojs .main-nav__toggler {
  display: none;
}

.main-nav--nojs .main-nav__list {
  display: block;
  position: static;
  min-height: 0;
}

.main-nav__list {
  padding: 0;
  list-style: none;
  align-items: center;
  display: flex;
  flex-direction: column;
  color: #000;
  text-decoration: none;
  margin: 0;
  z-index: 52;
  min-height: 57px;
}
@media (max-width: 768px) {
  .main-nav__list {
    position: absolute;
    display: flex;
    background-color: #FFF;
    top: 65px;
    left: -30px;
    right: -30px;
  }
}
@media (min-width: 768px) {
  .main-nav__list {
    flex-direction: row;
