html {
  width: 100vw;
}

body {
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
  margin: 0;
  width: 100vw;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  width: dfdfd;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

* {
  box-sizing: border-box;
}